/*=======================================
=====================Estilos generales */
body, html {
  padding: 0;
  margin: 0;
  position: relative;
  width: 100%;
  height: 100%;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
}

a:hover,
a:focus,
a:active {
  text-decoration: none;
}

.text-light {
  font-weight: 300;
}

.text-semi-bold {
  font-weight: 600;
}

.text-bold {
  font-weight: 700;
}

.full-width {
  width: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.divider {
  width: 95%;
  display: block;
  margin: 5px auto;
  height: 0;
  border-top: 1px solid #E1E1E1;
}

.section {
  padding: 60px 0;
}

/*====Tablas con CSS==*/
.div-table {
  display: table;
  height: auto;
}

.div-table-row,
.div-table-cell {
  height: 32px;
  line-height: 32px;
  text-align: center;
}

.div-table-row {
  display: table-row;
}

.div-table-cell {
  display: table-cell;
  border: 1px solid #fff;
  box-sizing: border-box;
}

.div-table-cell-c {
  background-color: #F5F5F5;
  color: #333;
}

.custom-input-file {
  overflow: hidden;
  position: relative;
  cursor: pointer;
  height: 150px;
  width: 150px;
  line-height: 150px;
  text-align: center;
  font-size: 70px;
  color: #E1E1E1;
  border-radius: 100%;
  border: 2px dashed #E1E1E1;
  display: block;
  margin: 0 auto;
}

.custom-input-file .input-file {
  margin: 0;
  padding: 0;
  outline: 0;
  font-size: 10000px;
  border: 10000px solid transparent;
  opacity: 0;
  filter: alpha(opacity=0);
  position: absolute;
  right: -1000px;
  top: -1000px;
  cursor: pointer;
}

.media-object {
  width: 64px;
  height: 64px;
  border-radius: 100%;
}

.res-msj {
  margin-top: 10px;
  display: none;
}

/*====Barra de navegacion==*/
.NavBar,
.NavBar-logo,
.NavBar-Nav,
.NavBar-Nav ul,
.NavBar-Nav ul div {
  height: 60px;
  line-height: 60px;
  position: relative;
}

.NavBar {
  border-bottom: 1px solid #E1E1E1;
}

.NavBar-logo,
.NavBar-Nav {
  position: absolute;
  top: 0;
}

.NavBar-logo {
  width: 25%;
  left: 0;
  color: #333;
  font-size: 27px;
  text-align: center;
}

.NavBar-logo figure,
.NavBar-logo figure img,
.NavBar-logo img {
  margin: 0 auto;
  padding: 0;
  height: 50px;
  display: block;
}

.NavBar-logo img,
.NavBar-logo figure img {
  padding-top: 5px;
  padding-bottom: 5px;
}

.NavBar-Nav {
  width: 75%;
  right: 0;
  text-align: right;
  padding-right: 10px;
}

.NavBar-Nav ul li {
  display: inline-block;
  margin: 0;
  padding: 0;
  position: relative;
  text-align: right;
}

.NavBar-Nav ul li a {
  box-sizing: border-box;
  text-align: center;
  font-size: 14px;
  padding: 0 5px;
  color: #999;
  position: relative;
  transition: all .3s ease-in-out;
  display: block;
}

.NavBar-Nav ul li a:hover {
  color: #333;
}

.NavBar-Nav-icon {
  width: 40px !important;
  height: 40px !important;
  line-height: 40px !important;
  border-radius: 50% !important;
  border: 2px solid rgb(145, 153, 161) !important;
  color: rgb(145, 153, 161) !important;
  text-align: center !important;
  font-size: 23px !important;
  margin: 0 !important;
  padding: 0 !important;
  background-color: rgb(220, 220, 220) !important;
  position: relative !important;
  cursor: pointer !important;
  top: 3px !important;
}

img.NavBar-Nav-icon {
  top: 0 !important;
  border-width: 1px !important;
}

/*====PopUpLogin==*/
.PopUpLogin {
  width: 400px;
  padding: 20px;
  background-color: #fff;
  border: 1px solid #ccc;
  position: absolute;
  top: 65px;
  right: 10px;
  z-index: 200;
  display: none;
}

.PopUpLogin a,
.PopUpLogin button {
  display: block;
  text-align: center;
  margin: 9px auto;
}

.PopUpLogin:after,
.PopUpLogin:before {
  bottom: 100%;
  right: 12px;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.PopUpLogin:after {
  border-color: rgba(136, 183, 213, 0);
  border-bottom-color: #fff;
  border-width: 10px;
  margin-right: 0px;
}

.PopUpLogin:before {
  border-color: rgba(194, 225, 245, 0);
  border-bottom-color: #ccc;
  border-width: 12px;
  margin-right: -2px;
}

.PopUpLogin-2 {
  width: 200px;
  padding: 10px 0;
}

.PopUpLogin-2 a,
.PopUpLogin-2 button {
  margin: 0;
  text-align: left;
  padding: 7px 10px;
  color: #333333;
  font-weight: 300;
  font-size: 14px;
}

.PopUpLogin-2 a:hover,
.PopUpLogin-2 button:hover {
  background-color: #F5F5F5;
}

/*====Footer==*/
.footer {
  background-color: #3C3C3C;
  color: #fff;
  padding: 50px 0;
  position: relative;
}

.footer ul li,
.social-post li {
  display: inline-block;
}

.footer ul li a {
  text-decoration: none;
  color: #fff;
}

.footer-social li a i {
  width: 40px;
  height: 40px;
  line-height: 40px;
  margin: 5px;
  font-size: 20px;
  border-radius: 50%;
  border: 1px solid #FFF;
  transition: all .3s ease-in-out;
}

.footer-social li a i:hover,
.footer-app-store li a:hover {
  transform: scale(1.2);
}

.footer-social li a i.fa-facebook:hover {
  background-color: #2B4073;
}

.footer-social li a i.fa-linkedin:hover {
  background-color: #278DD8;
}

.footer-social li a i.fa-google-plus:hover {
  background-color: #C63E24;
}

.footer-social li a i.fa-twitter:hover {
  background-color: #67CDEE;
}

.footer-social li a i.fa-youtube:hover {
  background-color: #D72823;
}

.footer-social li a i.fa-instagram:hover {
  background-color: #89A0B2;
}

.footer-app-store li a {
  height: 35px;
  line-height: 35px;
  border-radius: 4px;
  font-weight: 300;
  border: 1px solid #FFF;
  padding: 0 4px;
  transition: all .3s ease-in-out;
  display: block;
  margin: 5px;
}

.footer-app-store li a:hover {
  background-color: #76797C;
}

.footer-copyright li {
  font-size: 14px;
  font-weight: 300;
  margin: 5px;
}

