/*============================
=========commercial.html*/
.container-post {
  padding: 20px 0;
}

.post {
  border: 1px solid #ECEEEF;
  height: 270px;
  width: 250px;
  display: inline-block;
  margin: 5px;
  position: relative;
  float: left;
  color: #9199A1;
}

.post:hover {
  color: #9199A1;
  background-color: #F5F5F5;
}

.post-img,
.post-info {
  position: relative;
  left: 0;
}

.post > figure,
.post > img,
.post-img {
  height: 186px;
  top: 0;
}

.post > figure > img,
.post-img > img {
  width: 100%;
  height: 100%;
}

.post-info {
  height: 82px;
  bottom: 0;
  padding: 5px;
}

.post-info-title {
  font-size: 14px;
}

.post-info-price {
  color: #F09000;
  font-size: 16px;
  font-weight: 600;
}

.post-info-zone,
.post-info-date,
.post-info-like {
  position: absolute;
}

.post-info-zone,
.post-info-date {
  bottom: 7px;
  font-size: 12px;
}

.post-info-zone {
  left: 7px;
}

.post-info-date {
  right: 7px;
}

.post-info-like {
  right: 7px;
  top: 7px;
  height: 25px;
  width: 25px;
  line-height: 27px;
  text-align: center;
  border-radius: 100%;
  background-color: #fff;
  cursor: pointer;
}

.post-block {
  display: block;
  float: none;
  width: 100%;
  height: 186px;
}

.post-block .post-img,
.post-block .post-info {
  height: 186px;
  position: absolute;
  top: 0;
}

.post-block .post-img {
  width: 250px;
  left: 0;
  z-index: 2;
}

.post-block .post-info {
  right: 0;
  width: 100%;
  padding: 15px;
  padding-left: 265px;
  z-index: 1;
}

.post-block .post-info-zone {
  left: 15px;
}

.post-block .post-info-date {
  right: 15px;
}

