/*=========================
=========== Media Queries*/
@media (max-width: 991px) {
  .NavBar,
  .NavBar-logo,
  .btn-mobile-menu {
    height: 48px;
    width: 100%;
    line-height: 48px;
    position: relative;
    color: #921B80;
  }

  .NavBar-logo {
    text-align: left;
    padding-left: 10px;
  }

  .NavBar-logo figure,
  .NavBar-logo figure img,
  .NavBar-logo img {
    margin: 0;
    height: 40px;
  }

  .NavBar-logo img,
  .NavBar-logo figure img {
    padding-top: 4px;
    padding-bottom: 4px;
  }

  .btn-mobile-menu {
    width: 48px;
    right: 0;
    cursor: pointer;
    font-size: 24px;
    text-align: center;
  }

  .btn-search-mobile {
    right: 48px;
  }

  .NavBar-Nav {
    position: fixed;
    width: 100%;
    height: 100%;
    padding-right: 0;
    z-index: 999;
    pointer-events: none;
    opacity: 0;
    transition: all .3s ease-in-out;
  }

  .NavBar-Nav-show {
    pointer-events: auto;
    opacity: 1;
  }

  .NavBar-Nav ul,
  .NavBar-Nav-bg,
  .btn-mobile-menu {
    position: absolute;
    top: 0;
  }

  .NavBar-Nav ul {
    z-index: 2;
    height: 100%;
    width: 300px;
    right: 0;
    background-color: #fff;
  }

  .NavBar-Nav ul div {
    height: auto;
    z-index: 5;
  }

  .NavBar-Nav ul li,
  .NavBar-Nav ul li a {
    display: block;
    width: 100%;
    height: 40px;
    line-height: 40px;
  }

  .NavBar-Nav ul li a {
    text-align: left;
    padding-left: 20px;
    font-weight: 300;
  }

  .NavBar-Nav-bg {
    left: 0;
    height: 100%;
    z-index: 1;
    background-color: rgba(0, 0, 0, .5);
  }

  .PopUpLogin {
    display: none !important;
  }

  .header-menu-mobile {
    background-color: #EEEEEE;
    border: 1px solid #E1E1E1;
    padding: 25px 15px;
  }

  .header-menu-mobile-icon {
    position: relative !important;
    top: 0 !important;
    display: block !important;
    margin: 0 auto !important;
    width: 90px !important;
    height: 90px !important;
    line-height: 90px !important;
    font-size: 45px !important;
    cursor: inherit !important;
  }

  .header-menu-mobile-btn {
    display: block;
    width: 80%;
    margin: 9px auto;
  }

  .header-menu-mobile-close-btn {
    position: absolute;
    top: 0;
    left: 2px;
    width: 30px;
    height: 30px;
    line-height: 30px;
    cursor: pointer;
    text-align: center;
    font-size: 19px;
  }

  .Search-mobile {
    position: absolute;
    top: 48px;
    left: 0;
    border-top: 1px solid #E1E1E1;
    border-bottom: 1px solid #E1E1E1;
    padding: 20px;
    z-index: 99;
    background-color: #fff;
    display: none;
  }

  .Search-mobile button {
    display: block;
    width: 100%;
  }

  .menu-commercial {
    display: none;
  }
}

@media (max-width: 767px) {
  .div-table-cell-xs {
    width: 100%;
    display: block;
  }

  .bar-info-user {
    margin: 20px 0;
  }

  .user-menu-xs {
    display: none;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .menu-commercial,
  .user-menu-xs {
    display: inherit !important;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .menu-commercial,
  .user-menu-xs {
    display: inherit !important;
  }
}

@media (min-width: 1200px) {
  .menu-commercial,
  .user-menu-xs {
    display: inherit !important;
  }
}
